/* events */
.events {
  width: 100%;
  padding: 45px 0;
  position: relative;
  color: $white;

  &::after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, #000000 0%, rgba(0, 0, 0, 0.5) 100%);
  }
}

.events__bg-video {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.events__subtitle {
  font-weight: 700;
  font-size: 12px;
  line-height: 1.2;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 7px;
  position: relative;
  z-index: 3;
  margin-bottom: 45px;
}

.events__inner {
  position: relative;
  display: flex;
  justify-content:space-between;
  align-items:center;
  z-index: 3;
  margin-bottom: 45px;
}

.events__img-box {
  position: relative;
  width: 100%;
  max-width: 475px;
  height: auto;
  filter: drop-shadow(0px 0px 50px #000000);
  flex-shrink: .3;
}
.events__img-box--left{
  .circle {
    left:0;
    transform: translate(-40%,-50%);
  }
}
.events__img-box--right{
  .circle {
    right: 0;
    transform: translate(40%, -50%);
  }
}

.events__img {
  width: 100%;
  height: 100%;
  border-radius: 5px;
}
.events__banquet{
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  width: 80px;
  background-color: $black;
  font-weight: 400;
  font-size: 24px;
  text-transform: uppercase;
  letter-spacing: 2px;
  display: flex;
  align-items: center;
  text-indent: 20px;
  writing-mode: vertical-rl;
}
.events__party {
  position: absolute;
  top: 0;
  left: 0;
  width: 80px;
  height: 100%;
  background-color: $black;
  background-color: $black;
  font-weight: 400;
  font-size: 24px;
  text-transform: uppercase;
  letter-spacing: 2px;
  display: flex;
  align-items: center;
  text-indent: 20px;
  writing-mode: vertical-lr;
  transform: rotate(180deg);
}

.events__contacts,
.events__contacts-adapt{
  position: relative;
  z-index: 3;
  text-align: center;
  font-weight: 400;
  font-size: 12px;
  line-height: 1;
  color:$white-opacity;
  a:hover{
    background-color:$yellow-opacity;
    color:$white;
    border-radius: 3px;
    transition: all .3s;
  }
}
.events__popup{
  left: 48%;
  top: -40px;
  color:$white-opacity;
}
.events__contacts-adapt{
  display: none;
}
.events__popup-adapt{
  left: 47%;
  top: -30px;
  color:$grey;
}

@media(max-width:1400px){
  .events__img-box--left{
    .circle {
      left: 0;
      transform: translate(-23%,-50%);
    }
  }
  .events__img-box--right {
    .circle {
      right: 0;
      transform: translate(23%, -50%);
    }
  }
}
@media(max-width:1300px){
  .events__img-box--left{
    .circle {
      width: 32%;
      height: 32%;
      left: 0;
      transform: translate(10%,-190%);
      border:15px solid $yellow;
    }
  }
  .events__img-box--right{
    .circle {
      width: 32%;
      height: 32%;
      right: 0;
      transform: translate(-10%, -190%);
      border: 15px solid $yellow;
    }
  }
}
@media(max-width:1000px){
  .events__inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .events__img-box--left{
    margin-bottom: 65px;
    .circle {
      width: 52%;
      height: 52%;
      border:20px solid $yellow;
      left:0;
      transform: translate(-40%,-50%);
    }
  }
  .events__img-box--right{
    .circle {
      width: 52%;
      height: 52%;
      border: 20px solid $yellow;
      right: 0;
      transform: translate(40%, -50%);
    }
  }
  
}
@media(max-width:680px){
  .events__img-box--left{
    .circle {
      left: 0;
      transform: translate(-23%,-50%);
    }
  }
  .events__img-box--right{
    .circle {
      right: 0;
      transform: translate(23%, -50%);
    }
  }
}
@media(max-width:610px){
  .events__subtitle{
    margin-bottom: 55px;
  }
  .events__img-box--left {
    .circle {
      width: 32%;
      height: 32%;
      border: 15px solid $yellow;
      left: 0;
      transform: translate(10%, -190%);
      border: 15px solid $yellow;
      filter: drop-shadow(0px 0px 30px rgba(0, 0, 0, 0.5));
    }
  }
  .events__img-box--right{
    .circle {
      width: 32%;
      height: 32%;
      border: 15px solid $yellow;
      right: 0;
      transform: translate(-10%, -190%);
      border: 15px solid $yellow;
      filter: drop-shadow(0px 0px 30px rgba(0, 0, 0, 0.5));
    }
  }
  .events__contacts{
    display: none;
  }
  .events__contacts-adapt{
    display: block;
    line-height: 1.5;
    text-align: center;
  }
   
}
@media(max-width:450px) {
  .events__img-box {
    filter: drop-shadow(0px 0px 20px rgba(0, 0, 0, 1));
  }
  .events__banquet,
  .events__party{
    font-size: 18px;
    width: 60px;
  }
}