/* order */
.order {
  width: 100%;
  margin: 0 auto;
  padding: 100px 0 45px 0;
}

.order__inner {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 50px;
}

.order__form-box {
  width: 100%;
  max-width: 565px;
  margin-right: 20px;
}

.order__title {
  position: relative;
  margin-bottom: 60px;

  &::after {
    content: '';
    position: absolute;
    width: 60px;
    height: 5px;
    left: 50%;
    transform: translateX(-50%);
    bottom: -14px;
    background: $yellow;
    border-radius: 50px;
  }
}

.form {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
}

.form__input {
  width: calc(50% - 5px);
  max-width: 277px;
  height: 60px;
  margin-bottom: 10px;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.35;
  background: $gray;
  border-radius: 5px;
  padding: 20px 10px 20px 20px;
  border: solid 2px transparent;
  transition: all .3s;
  color: $grey;
  &::placeholder {
    color: $grey;
  }

  &:focus {
    border: solid 2px $yellow;
    background-color: #dedcdc;
    transition: all .3s;
  }
  &:hover{
    transition: all .3s;
    background-color: #dedcdc;
  }

  &:focus::placeholder {
    font-size: 10px;
    transform: translate(-10px, -20px);
    transition: all .3s;
  }
}
.input-date{
  appearance: none;
  color:$grey;
}
.input-name{
  text-transform: capitalize;
}
.form__select {
  position: relative;
  width: calc(50% - 5px);
  max-width: 277px;
  height: 60px;
  margin-bottom: 10px;

  &::after {
    content: '';
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
    width: 14px;
    height: 9px;
    background-image: url('../img/select-arrow.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    pointer-events: none;
  }

  select {
    width: 100%;
    height: 100%;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.35;
    background: $gray;
    color: $grey;
    border-radius: 5px;
    padding: 20px 10px 20px 20px;
    border: solid 2px transparent;
    transition: all .3s;
    appearance: none;

    &:focus {
      border: solid 2px $yellow;
      outline: solid 2px transparent;
      background-color: #dedcdc;
      transition: all .3s;
    }
    &:hover{
      background-color: #dedcdc;
      transition: all .3s;
    }
   
  }
}
.error{
  border: solid 2px $error;
}
.select-error{
  outline: solid 2px $error;
 
}
.form__btn {
  width: 100%;
  height: 60px;
  border-radius: 5px;
  background-color: $yellow;
  color: $white;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.6;
  text-transform: uppercase;

  &:hover {
    transition: all .3s;
    background-color: #e8ae00;
  }
}

.order__img-box {
  position: relative;
  width: 100%;
  max-width: 475px;
  height: auto;
  filter: drop-shadow(0px 0px 50px rgba(0, 0, 0, 0.5));
  flex-shrink: .3;

  .circle {
    right: 0;
    transform: translate(40%, -50%);
  }
}

.order__img {
  width: 100%;
  height: 100%;
  border-radius: 5px;
}

.order__contacts {
  position: relative;
  text-align: center;
  a:hover{
    background-color:$yellow;
    border-radius: 3px;
    transition: all .3s;
  }
  
}
.order__popup{
  left: 65%;
  top: -40px;
  color:$black;
}

.order__contacts--adapt {
  display: none;
  line-height: 1.5;
  max-width: 280px;
  margin: 0 auto;
  text-align: left;
  a:hover{
    background-color:$yellow;
    border-radius: 3px;
    transition: all .3s;
  }
}
.order__popup-adapt{
  left: 50%;
  top: -30px;
  color:$black;
}

@media(max-width:1400px) {
  .order__img-box {
    .circle {
      right: 0;
      transform: translate(23%, -50%);
    }
  }
}

@media(max-width:1300px) {
  .order__img-box {
    .circle {
      width: 32%;
      height: 32%;
      right: 0;
      transform: translate(-10%, -190%);
      border: 15px solid $yellow;
    }
  }
}

@media(max-width:1000px) {
  .order__inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .order__form-box {
    margin-right: 0;
    margin-bottom: 65px;

  }

  .order__img-box {
    .circle {
      width: 52%;
      height: 52%;
      border: 20px solid $yellow;
      right: 0;
      transform: translate(40%, -50%);
    }
  }
}

@media(max-width:768px) {
  .order__contacts {
    display: none;
  }

  .order__contacts--adapt {
    display: block;
  }
}

@media(max-width:680px) {
  .order__img-box {
    .circle {
      right: 0;
      transform: translate(23%, -50%);
    }
  }
}

@media(max-width:610px) {
  .order {
    padding: 50px 0 25px 0;
  }

  .order__title {
    margin-bottom: 60px;
  }

  .order__img-box {
    .circle {
      width: 32%;
      height: 32%;
      border: 15px solid $yellow;
      right: 0;
      transform: translate(-10%, -190%);
      border: 15px solid $yellow;
      filter: drop-shadow(0px 0px 30px rgba(0, 0, 0, 0.5));
    }
  }
}

@media(max-width:500px) {

  .form__input,
  .form__select {
    width: 100%;
    max-width: 500px;
  }

  .order__contacts--adapt {
    font-size: 16px;
  }
}

@media(max-width:450px) {
  .order {
    padding: 40px 0 45px 0;
  }
  .order__img-box {
    filter: drop-shadow(0px 0px 20px rgba(0, 0, 0, 1));
  }
  .order__title {
    margin-bottom: 40px;
  }
}