/* special offer */
.offer {
  width: 100%;
  padding: 40px 0 95px 0;
  position: relative;
  color: $white;
  overflow: hidden;

  &::after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, #000000 0%, rgba(0, 0, 0, 0.4) 100%);
  }
}

.offer__bg-video {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.offer__subtitle {
  font-weight: 700;
  font-size: 12px;
  line-height: 1.2;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 7px;
  position: relative;
  z-index: 3;
  margin-bottom: 45px;
}

.offer__inner {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  z-index: 3;
}

.offer__img-box {
  position: relative;
  width: 100%;
  max-width: 475px;
  height:475px;
  filter: drop-shadow(0px 0px 50px #000000);
  flex-shrink: .3;

  .circle {
    left: 0;
    transform: translate(-40%, -50%);
  }
}

.offer__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 5px;
}

.offer__content {
  width: 50%;
  max-width: 600px;
  margin-left: 30px;
}

.offer__title {
  position: relative;
  margin-bottom: 55px;

  &::after {
    content: '';
    position: absolute;
    width: 60px;
    height: 5px;
    left: 50%;
    transform: translateX(-50%);
    bottom: -14px;
    background: $yellow;
    border-radius: 50px;
  }
}

.offer__text:not(:last-child) {
  margin-bottom: 35px;
}


@media(max-width:1400px) {
  .offer__img-box {
    .circle {
      left: 0;
      transform: translate(-23%, -50%);
    }

  }
}

@media(max-width:1300px) {
  .offer__img-box {
    .circle {
      width: 32%;
      height: 32%;
      left: 0;
      transform: translate(10%, -190%);
      border: 15px solid $yellow;
    }
  }
}

@media(max-width:1000px) {
  .offer__inner {
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
  }

  .offer__content {
    width: 100%;
    max-width: 600px;
    margin-left: 0;
    margin-bottom: 65px;
  }

  .offer__img-box {
    .circle {
      width: 52%;
      height: 52%;
      border: 20px solid $yellow;
      left: 0;
      transform: translate(-40%, -50%);
    }
  }
}

@media(max-width:768px) {
  .offer {
    padding: 40px 0 66px 0;
  }

  .offer__subtitle {
    margin-bottom: 20px;
  }
}

@media(max-width:680px) {
  .offer__img-box {
    .circle {
      left: 0;
      transform: translate(-23%, -50%);
    }

  }
}

@media(max-width:610px) {
  .offer__title {
    margin-bottom: 25px;
  }

  .offer__img-box {
    filter: drop-shadow(0px 0px 30px rgba(0, 0, 0, 1));
  }

  .offer__text:not(:last-child) {
    margin-bottom: 16px;
  }

  .offer__img-box {
    .circle {
      width: 32%;
      height: 32%;
      border: 15px solid $yellow;
      left: 0;
      transform: translate(10%, -190%);
      border: 15px solid $yellow;
      filter: drop-shadow(0px 0px 50px rgba(0, 0, 0, 0.5));
    }
  }
}
@media(max-width:450px) {
  .offer__img-box {
   height: auto;
  }
}
@media(max-width:450px) {
  .offer__img-box {
    filter: drop-shadow(0px 0px 20px rgba(0, 0, 0, 1));
  }
}