/* global styles*/

body {
  font-family: $font-text;
  font-size: $text-size;
  font-weight: 400;
  font-style: normal;
}

.wraper {
  max-width: $wrap;
  margin: 0 auto;
}

.container {
  margin: 0 auto;
  max-width: $cont;
  padding: 0 15px;
}
.no-scroll{
  overflow: hidden;
}
.error{
  border: solid 2px $error;
}
.select-error{
  outline: solid 2px $error;
 
}
:root{
  scroll-behavior: smooth;
}
.title {
  font-family:$font-title;
  font-weight: 400;
  font-size: 42px;
  line-height:1.15;
  text-transform: uppercase;
  text-align: center;
}
.text{
  font-weight: 400;
  font-size: 18px;
  line-height: 1.8;
}

.bold-text{
  font-weight: 700 !important;
}

.circle{
  position: absolute;
  top: 50%;
  width: 52%;
  height: 52%;
  border: 20px solid #E8C300;
 filter: drop-shadow(0px 0px 100px rgba(0, 0, 0, 0.5));
 border-radius: 50%;
}
.phone-popup{
  position: absolute;
  padding: 6px 22px;
  border-radius:3px;
  background-color: $yellow-opacity;
  font-weight: 700;
  font-size: 12px;
  text-transform: uppercase;
  white-space:nowrap ;
  letter-spacing: 1px;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  transform: translateY(20px);
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
 
  &::after{
    content:'';
    position: absolute;
    left: 8px;
    bottom:-10px;
    width: 0;
    height: 0;
    border-top:solid 10px $yellow-opacity;
    border-left:solid 10px transparent;
    border-right:solid 10px  transparent;
  }
}
.show-popup{
  transform: translateY(0px);
  opacity: 1;
  transition: all .5s;
  transition-delay: 1s;
}
.slider-pagination {
  width: 100%;
  height: 10px;
  position: absolute;
  left: 0;
  bottom: 45px;
  z-index: 3;
  display: flex;
  justify-content: center;
  align-items: center;

  .swiper-pagination-bullet {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: $white-opacity;
    margin: 0 10px;
    cursor: pointer;
    transition: all .3s;

    &:hover {
      background-color: $white;
      transition: all .3s;
    }
  }

  .swiper-pagination-bullet-active {
    background-color: $white;
  }
}

@media(max-width:768px){
  .title{
    font-size: 35px;
  }
  .text{
    font-size: 17px;
  }
}
@media(max-width:600px){
  .title{
    font-size: 30px;
  }
  .text{
    font-size: 16px;
  }
}
@media(max-width:450px){
  .title{
    font-size: 25px;
  }
  .text{
    font-size: 15px;
  }
} 
@media(max-width:400px){
  .title{
    font-size: 21px;
  }
  .text{
    font-size: 14px;
  }
} 