/* team */
.team {
  width: 100%;
  padding: 40px 0 126px 0;
  position: relative;
  color: $white;
  overflow: hidden;
  &::after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, #000000 0%, rgba(0, 0, 0, 0.5) 100%);
  }
}
.team__bg-video {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.team__subtitle {
  font-weight: 700;
  font-size: 12px;
  line-height: 1.2;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 7px;
  position: relative;
  z-index: 3;
  margin-bottom: 45px;
}
.team__inner {
  position: relative;
  display: flex;
  justify-content:space-between;
  align-items:flex-end;
  z-index: 3;
}
.team__img-box {
  position: relative;
  width: 100%;
  max-width: 475px;
  height: auto;
  filter: drop-shadow(0px 0px 50px #000000);
  flex-shrink: .3;

  .circle {
    left:0;
    transform: translate(-40%,-50%);
  }
}
.team__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 5px;
}
.team__content {
  width: 50%;
  max-width: 600px;
  margin-left: 30px;
}
.team__title {
  position: relative;
  margin-bottom: 55px;
  &::after{
    content:'';
    position: absolute;
    width: 60px;
    height: 5px;
    left: 50%;
    transform: translateX(-50%);
    bottom: -14px;
    background: $yellow;
    border-radius: 50px;
  }

}
.team__text:not(:last-child){
  margin-bottom: 35px;
}

@media(max-width:1400px){
  .team__img-box{
    .circle {
      left: 0;
      transform: translate(-23%,-50%);
    }

  }
}
@media(max-width:1300px){
  .team__img-box{
    .circle {
      width: 32%;
      height: 32%;
      left: 0;
      transform: translate(10%,-190%);
      border:15px solid $yellow;
    }
  }
}
@media(max-width:1000px){
  .team__inner {
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
  }
  .team__content {
    width: 100%;
    max-width: 600px;
    margin-left: 0;
    margin-bottom: 65px;
  }
  .team__img-box{
    .circle {
      width: 52%;
      height: 52%;
      border:20px solid $yellow;
      left:0;
      transform: translate(-40%,-50%);
    }

  }
}
@media(max-width:768px){
  .team {
    padding: 40px 0 66px 0;
  }
  .team__subtitle{
    margin-bottom: 20px;
  }
}
@media(max-width:680px){
  .team__img-box{
    .circle {
      left: 0;
      transform: translate(-23%,-50%);
    }

  }
}
@media(max-width:610px){
  .team__title{
    margin-bottom: 25px;
}
    .team__img-box {
      filter: drop-shadow(0px 0px 30px rgba(0, 0, 0, 1));
  }
  .team__text:not(:last-child) {
    margin-bottom: 16px;
  }
  .team__img-box{
    .circle {
      width: 32%;
      height: 32%;
      border:15px solid $yellow;
      left: 0;
      transform: translate(10%,-190%);
      border:15px solid $yellow;
      filter: drop-shadow(0px 0px 50px rgba(0, 0, 0, 0.5));
    }
} 
} 
@media(max-width:450px){
  .team__img-box {
    filter: drop-shadow(0px 0px 20px rgba(0, 0, 0,1));
}
}   