/* contacts */
.contacts {
  width: 100%;
  padding: 50px 0 90px;
}

.contacts__title {
  position: relative;
  margin-bottom: 60px;

  &::after {
    content: '';
    position: absolute;
    width: 60px;
    height: 5px;
    left: 50%;
    transform: translateX(-50%);
    bottom: -14px;
    background: $yellow;
    border-radius: 50px;
  }
}

.contacts__text {
  margin: 0 auto 40px;
  max-width: 515px;
  text-align: center;
}

.contacts__form {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.form__input-box {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contacts__form-input +.contacts__form-input {
  margin-left: 15px;
}

.contacts__form-input,
.contacts__textarea {
  width: 100%;
  max-width: 380px;
  height: 60px;
  margin-bottom: 15px;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.35;
  background: $gray;
  color: $light-black;
  border-radius: 5px;
  padding: 0 20px;
  border: solid 2px transparent;
  transition: all .3s;

  &::placeholder {
    color: $grey;
  }

  &:focus {
    border: solid 2px $yellow;
    background-color: #dedcdc;
    transition: all .3s;
  }

  &:hover {
    transition: all .3s;
    background-color: #dedcdc;
  }

  &:focus::placeholder {
    font-size: 10px;
    transform: translate(-10px, -20px);
    transition: all .3s;
  }
}
.contacts-name{
  text-transform: capitalize;
}
.error{
  border: solid 2px $error;
}
.contacts__textarea {
  width: 100%;
  max-width: 100%;
  height: 120px;
  padding: 20px;
  transition: all .3s;
  resize: none;
}

.contacts__info-box {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.contacts__info {
  position: relative;
  height: 60px;
  width: 100%;
  max-width: 500px;
  margin-right: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.contacts__popup{
  left: 45%;
  top: -12px;
  color:$grey;
}
.contacts__info-item + .contacts__info-item{
margin-left: 15px;
}
.contacts__info-item {
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 700;
  font-size: 12px;
  line-height: 12px;
  color:$grey;
  a,
  span{
    display: inline-block;
  }
  .mail{
    transform: translate(-4px,4px);
  }
  .phone{
    margin-right: 2px;
  }
  a:hover{
    background-color:$yellow;
    border-radius: 3px;
    transition: all .3s;
  }
}
.contacts__info .contacts__info-item:nth-child(1) {
  margin-left: -2px;
}
.contacts__svg {
  margin-right: 3px;
}

.contacts__btn {
  width: 100%;
  max-width: 585px;
  height: 60px;
  border-radius: 5px;
  background-color: $yellow;
  color: $white;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.6;
  text-transform: uppercase;

  &:hover {
    transition: all .3s;
    background-color: #e8ae00;
  }
}

@media(max-width:1050px){
  .contacts__info-box {
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
  }
  .contacts__btn {
    max-width: 100%;
    margin-bottom: 20px;
  }
}
@media(max-width:768px){
  .contacts {
    padding: 25px 0 45px;
  }
  .contacts__title{
    margin-bottom: 30px;
  }
  .contacts__text {
    margin: 0 auto 25px;
  }
  .form__input-box {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .contacts__form-input+.contacts__form-input {
    margin-left: 0;
  }
  .contacts__form-input{
    max-width: 100%;
  }
}
@media(max-width:600px){
  .contacts__btn{
    margin-bottom: 30px;
  }
  .contacts__info {
    width: 100%;
    height: 75px;
    max-width: 500px;
    margin-right:0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }
  .contacts__info-item {
    height: 25px;
    width: 220px;
    justify-content:flex-start;
  }
  .contacts__popup{
    left: 35%;
    top: -26px;
  }
  .contacts__info-item + .contacts__info-item{
    margin-left: 0;
    }
}
