/* about*/
.about {
  padding: 100px 0;
  color: $black;
}

.about__inner {
  display: flex;
  justify-content:space-between;
  align-items:flex-end;
}

.about__content {
  width: 50%;
  max-width: 600px;
  margin-right: 30px;
}

.about__title {
  position: relative;
  margin-bottom: 57px;
  &::after{
    content:'';
    position: absolute;
    width: 60px;
    height: 5px;
    left: 50%;
    transform: translateX(-50%);
    bottom: -14px;
    background: $yellow;
    border-radius: 50px;
  }
}

.about__text:not(:last-child) {
  margin-bottom: 26px;
}

.about__img-box {
  position: relative;
  width: 100%;
  max-width: 475px;
  height: auto;
  filter: drop-shadow(0px 0px 50px rgba(0, 0, 0, 0.5));
  flex-shrink: .3;

  .circle {
    right: 0;
    transform: translate(40%,-50%);
  }
}

.about__img {
  width: 100%;
  height: 100%;
  border-radius: 5px;
}

@media(max-width:1400px){
  .about__img-box{
    .circle {
      right: 0;
      transform: translate(23%,-50%);
    }

  }
}
@media(max-width:1300px){
  .about__img-box{
    .circle {
      width: 32%;
      height: 32%;
      right: 0;
      transform: translate(-10%,-190%);
      border:15px solid $yellow;
    }
  }
}

@media(max-width:1000px){
  .about__inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .about__content {
    width: 100%;
    max-width: 600px;
    margin-right: 0;
    margin-bottom: 65px;
  }
  .about__img-box{
    .circle {
      width: 52%;
      height: 52%;
      border:20px solid $yellow;
      right: 0;
      transform: translate(40%,-50%);
    }

  }
}
@media(max-width:768px){
  .about {
    padding: 50px 0;
  }
}
@media(max-width:680px){
  .about__img-box{
    .circle {
      right: 0;
      transform: translate(23%,-50%);
    }

  }
}
@media(max-width:610px){
  .about__title{
    margin-bottom: 27px;
  }
    .about__img-box {
      filter: drop-shadow(0px 0px 30px rgba(0, 0, 0, 0.5));
  }
  .about__text:not(:last-child) {
    margin-bottom: 16px;
  }
  .about__img-box{
    .circle {
      width: 32%;
      height: 32%;
      border:15px solid $yellow;
      right: 0;
      transform: translate(-10%,-190%);
      border:15px solid $yellow;
      filter: drop-shadow(0px 0px 50px rgba(0, 0, 0, 0.5));
    }
} 
} 

@media(max-width:450px){
    .about__img-box {
      filter: drop-shadow(0px 0px 20px rgba(0, 0, 0, 0.5));
  }
}   