/* Header styles */
.header {
  width: 100%;
  padding: 16px 0 89px 0;
  position: relative;
  color: $white;

  &::after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, #000000 0%, rgba(0, 0, 0, 0.5) 100%);
  }
}

.header__bg-video {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.header__inner {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 3;
}

.header__nav {
  position: relative;
  width: 100%;
  height: 120px;
  margin-bottom: 177px;
  display: flex;
  justify-content: space-between;
}

.header__menu {
  width: 100%;
  height: 120px;
  padding: 0 100px;
  display: flex;
  justify-content: space-between;
}

.header__logo {
  width: 83px;
  height: 100px;
}

.header__menu-link {
  position: relative;
  display: inline-block;
  padding: 25px 0 20px 0;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.5;
  text-transform: uppercase;

  &:hover {
    color: $yellow;
    transition: all .3s;
  }

  &::before {
    content: '';
    position: absolute;
    top: 25px;
    left: 50%;
    transform: translateX(-50%);
    width: 130%;
    height: 5px;
    background-color: $yellow;
    opacity: 0;
    transition: all .3s;
  }

  &:hover::before {
    top: -16px;
    opacity: 1;
    transition: all .3s;
  }
}

.header__logo--two {
  display: none;
}

.header__burger {
  display: none;
  width: 38px;
  height: 24px;
  position: absolute;
  right: 30px;
  top: 30px;
  transition: all .3s;
  z-index: 10;

  &:hover .header__burger-span--one::before {
    transition: all .3s;
    transition-delay: .1s;
    width: 100%;
  }
  &:hover .header__burger-span--two::before {
    transition: all .3s;
    transition-delay: .2s;
    width: 100%;
  }
  &:hover .header__burger-span--three::before {
    transition: all .3s;
    transition-delay: .3s;
    width: 100%;
  }
}

.header__burger-span {
  width: 38px;
  height: 3px;
  border-radius: 1.5px;
  background-color: $white;
  position: absolute;
  left: 0;
  transition: all .3s;

  &::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 0%;
    height: 100%;
    border-radius: 1.5px;
    background-color: $yellow;
    transition: all .3s;
  }
}

.header__burger-span--one {
  top: 0;
}

.header__burger-span--one::before {
  transition-delay: .3s;
}

.header__burger-span--two {
  top: 50%;
}

.header__burger-span--two::before {
  transition-delay: .2s;
}

.header__burger-span--three {
  top: 100%;
}

.header__burger-span--three::before {
  transition-delay: .1s;
}

.span-one--active {
  top: 50%;
  transform: rotate(45deg);
}

.span-two--active {
  top: 50%;
  transform: rotate(-45deg);
}

.span-three--active {
  display: none;
}

.header__content-box {
  margin: 0 auto;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.header__schedule {
  font-weight: 700;
  font-size: 12px;
  line-height: 1;
  writing-mode: vertical-rl;
  transform: rotate(180deg);
  padding-bottom: 60px;
}

.header__content {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  margin-right: -15px;
  text-align: center;
}

.header__subtitle {
  font-weight: 700;
  font-size: 18px;
  line-height: 1;
  text-transform: uppercase;
  margin-bottom: 50px
}

.header__title {
  font-family: $font-title;
  font-weight: 400;
  font-size: 72px;
  line-height: 1;
  color: $yellow;
  max-width: 420px;
  margin-bottom: 65px;
}

.header__btn-box {
  position: relative;
  width: 100%;
  max-width: 440px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 82px;

  &::before {
    content: '';
    position: absolute;
    top: -30px;
    left: 50%;
    transform: translateX(-50%);
    width: 60px;
    height: 5px;
    background-color: $yellow;
    border-radius: 2.5px;
  }
}

.header__btn {
  width: 200px;
  height: 60px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.5;
  text-transform: uppercase;
  border: 1px solid rgb(139, 136, 135);
  transition: all .3s;

  &:hover {
    transition: all .3s;
    background-color: $yellow;
    border: 1px solid transparent;
  }
}

.header__down-arrow {
  width: 62px;
  height: 62px;
}

.down-arrow-svg:hover ellipse {
  transition: all .3s;
  fill: $yellow;
}

.down-arrow-svg:hover circle {
  transition: all .3s;
  stroke: $yellow;
  stroke-opacity: 1;
}

.header__social {
  display: flex;
  flex-direction: column;
  padding-top: 60px;
}

.header__social-link:not(:last-child) {
  margin-bottom: 20px;
}

.header__social-svg{
  width: 62px;
  height: 62px;
}

.header__social-svg:hover path {
  transition: all .3s;
  fill: $yellow;
}

.header__social-svg:hover circle {
  transition: all .3s;
  stroke: $yellow;
  stroke-opacity: 1;
}

@media (max-width:768px) {
  .header__logo--two {
    display: block;
  }
  .header__logo--one {
    display:none;
  }
  .header__burger {
    display: block;

  }
  .header__nav {
    justify-content: center;
    margin-bottom: 50px;
  }
  .header__menu{
    position: fixed;
    left: 0;
    top: 0;
    z-index: 9;
    transform: translateY(-400%);
    transition: all .5s;
    width: 100%;
    max-width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding-top: 100px;
    background-color: $black;
   
  }
  .header__menu--active{
    transform: translateY(0);
    transition: all .5s;
  }
  .header__menu-link{
  font-size: 16px;
  padding: 18px 0 18px 0;

  &:hover::before {
    top: 0px;
    opacity: 1;
    transition: all .3s;
  }
  }
  .header__content{
    margin-right: -45px;
  }
  .header__btn-box{
    flex-direction: column;
    margin-bottom: 40px;
  }
  .header__btn:not(:last-child){
    margin-bottom: 20px;
  }
  .header__title{
    font-size: 62px;
  }
  .header__subtitle{
    font-size: 17px;
    margin-bottom: 30px;
  }
}
@media(max-width:600px){
  .header__nav {
    margin-bottom: 30px;
  }
  .header__logo {
    width: 69px;
    height: 83px;
  }
  .header__schedule {
    padding-bottom: 150px;
  }
  .header__content{
    margin-right: -31px;
  }
  .header__social {
    padding-top: 150px;
  }
  
  .header__title{
    font-size: 50px;
  }
  .header__subtitle{
    font-size: 16px;
    margin-bottom: 25px;
  }
  .header__btn {
    width: 166px;
    height: 50px;
  }
  .header__down-arrow {
    width: 52px;
    height: 52px;
  }
  .header__social-svg{
    width: 52px;
    height: 52px;
  }
}
@media(max-width:450px){
  .header__nav {
    margin-bottom: 0px;
  }
  .header__burger{
    right: 10px;
  }
  .header__content-box {
    position: relative;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .header__schedule {
    padding-bottom: 0px;
    position: absolute;
    left: 0px;
    bottom: 90px;
  }
  .header__content{
    margin-right: 0px;
  }
  .header__title{
    font-size: 45px;
  }
  .header__subtitle{
    font-size: 15px;
    margin-bottom: 15px;
  }
  .header__down-arrow {
  transform: translateY(100px);
  }
  .header__social {
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: row;
    padding-top: 0px;
    transform: translateY(-50px);
  }
  .header__social-link:not(:last-child) {
    margin-bottom: 0px;
    margin-right: 20px;
  }
}
@media(max-width:400px){
  .header__burger{
    right: 10px;
  }
  .header__title{
    font-size: 40px;
  }
  .header__subtitle{
    font-size: 14px;
    margin-bottom: 10px;
  }
}