/* menu */
.menu {
  width: 100%;
  padding: 65px 0 75px;
}

.menu__title {
  position: relative;
  margin-bottom: 60px;

  &::after {
    content: '';
    position: absolute;
    width: 60px;
    height: 5px;
    left: 50%;
    transform: translateX(-50%);
    bottom: -14px;
    background: $yellow;
    border-radius: 50px;
  }
}

.menu__text {
  margin: 0 auto;
  text-align: center;
  max-width: 565px;
  margin-bottom: 45px;
}

.tabs-container {
  position: relative;
  margin: 0 auto 60px;
  max-width: $cont;
  height: 60px;
  overflow-y: hidden;
  overflow-x: auto;

  &::-webkit-scrollbar {
    height: 5px;
  }

  &::-webkit-scrollbar-track {
    background-color: $light-grey;

  }

  &::-webkit-scrollbar-thumb {
    background: $yellow;
    border-radius: 2px;

  }
}

.tabs {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 15px;

  &__item {
    min-width: 163px;
    height: 47px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-weight: 700;
    font-size: 12px;
    line-height: 1.5;
    text-transform: uppercase;
    color: $light-black;
    cursor: pointer;
    transition: all .3s;

    &:hover {
      background-color: $yellow;
      transition: all .3s;
    }

    &.active {
      background-color: $yellow;
      transition: all .3s;
    }
  }
}

.tabs__item +.tabs__item {
  border-left: 2px solid $light-grey;
}

.overflow {
  left: 0;
  transform: none;
}

.menu__sliders {
  position: relative;
  margin: 0 auto;
  max-width: 1400px;
  height: 460px;
  overflow: hidden;
}

.menu__slider-block {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1;
  opacity: 0;
  transition: all .5s;

  &.show {
    z-index: 2;
    opacity: 1;
  }
}
.menu__slider .swiper-wrapper { 
  flex-wrap: wrap; 
}

.menu__slider .swiper-slide{
  min-height:175px;
  margin-bottom: 55px;
  display: flex;
  justify-content: center;
}

.menu__item {
  width: 100%;
  max-width: 500px;
  min-height: 170px;
  margin: 0 10px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.menu__img-box {
  width: 251px;
  height: 170px;
  border: 4px solid $yellow;
  margin-right: 25px;
  flex-shrink: 0;
}

.menu__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.menu__content {
  max-width: 221px;
  height:170px;
  padding-top: 10px;
  overflow-y: auto;
  &::-webkit-scrollbar {
    width: 3px;
  }
  &:hover::-webkit-scrollbar {
    width: 4px;
    transition: all .5s;
  }
  &::-webkit-scrollbar-track {
    background-color: $light-gray;
  }

  &::-webkit-scrollbar-thumb {
    background:transparent;
    border-radius: 1px;
  }
  &:hover::-webkit-scrollbar-thumb {
    background: $yellow-light;
    transition: all .5s;
  }
 
}

.menu__dish-name {
  display: inline-block;
  position: relative;
  max-width: 200px;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.4;
  text-transform: uppercase;
  color: $light-black;
  margin-bottom: 5px;
  border-bottom: solid 1px $light-black; ;
}

.menu__dish-price {
  font-weight: 400;
  font-size: 18px;
  line-height: 1.4;
  text-transform: uppercase;
  color: $light-black;
  margin-bottom: 21px;
}

.menu__description {
  font-weight: 400;
font-size: 12px;
line-height: 1.2;
color: $grey;
margin-bottom: 10px;
padding-right: 5px;
}
.menu-pagination{
  width: 100%;
  height: 10px;
  position: absolute;
  left: 0;
  bottom:0;
  z-index: 3;
  display: flex;
  justify-content: center;
  align-items: center;
  .swiper-pagination-bullet{
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: $grey;
    margin: 0 10px;
    cursor: pointer;
    transition: all .3s;
    &:hover{
      background-color: $yellow;
      transition: all .3s;
    }
  }
  .swiper-pagination-bullet-active{
    background-color: $yellow;
  }
}


@media(max-width:999px){
  .menu__sliders {
   height: 400px;
  }
  .menu__slider .swiper-wrapper { 
    flex-wrap: nowrap; 
  }
  .menu__item {
   flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .menu__img-box {
    margin-right:0;
  }
  .menu__content {
    max-width: 251px;
    margin-top: 10px;
  }
}
@media(max-width:768px){
  .menu {
    padding: 35px 0 40px;
  }
  .menu__title {
    margin-bottom:30px;
  }
  .menu__text{
    margin-bottom: 30px;
  }
}